.popup-box_opened {
  display: flex;
  width: 100%;
  height: 100%;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  /*display: flex;*/



  visibility: visible;
  opacity: 1;
  pointer-events: visible;
  user-select: auto;
  transition: visibility 0s linear 0s, opacity linear .4s;
  /*z-index: 2;*/
  /*transition: visibility/* 0ms linear 1ms*//*, opacity linear 4s;*/
}
